import React from 'react';
import { connect } from 'react-redux';
import { Row } from 'reactstrap';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import { Colxx } from '../../../../components/common/CustomBootstrap';
import RecentHack from '../../../../components/common/RecentHack';
import hacks from '../../../../data/hacks';
import EditProfileActions from './EditProfileActions';
import UserCard from './UserCard';

const ProfileView = ({ match, currentUser, profileData }) => (
  <>
    <Row>
      <Colxx xxs="12">
        <Row>
          <Colxx className="mb-0">
            <h1>{profileData.full_name}</h1>
            <EditProfileActions />
          </Colxx>
        </Row>

        <Breadcrumb match={match} />

        <Row>
          <Colxx xxs="12" lg="5" className="mb-4 col-left">
            <UserCard currentUser={currentUser} />
          </Colxx>

          <Colxx xxs="12" lg="7" className="mb-4 col-right">
            <Row>
              {hacks.map((hack) => {
                return (
                  <Colxx xxs="12" lg="6" className="mb-4" key={hack.id}>
                    <RecentHack data={hack} />
                  </Colxx>
                );
              })}
            </Row>
          </Colxx>
        </Row>
      </Colxx>
    </Row>
  </>
);

const mapStateToProps = ({ authUser }) => {
  const { currentUser, profileData } = authUser;
  return { currentUser, profileData };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(ProfileView);
