/* eslint-disable camelcase */
import React from 'react';

import {
  Badge,
  Button,
  Card,
  CardBody,
  CardSubtitle,
  CardText,
  CardTitle,
  Row,
} from 'reactstrap';
import { adminRoot } from '../../constants/defaultValues';
import IntlMessages from '../../helpers/IntlMessages';
import { Separator } from './CustomBootstrap';

const getStatusBadgeColor = (status) => {
  if (status === 0) return 'outline-success';
  if (status === 1) return 'outline-warning';
  if (status === 2) return 'outline-danger';
  return 'primary';
};

const getStatus = (status) => {
  if (status === 0) return 'UPCOMING';
  if (status === 1) return 'ON GOING';
  if (status === 2) return 'COMPLETED';
  return 'primary';
};

const RecentHack = ({ data, participated }) => {
  const {
    id,
    name,
    status,
    hackathon_website,
    application_start,
    application_end,
    hackathon_start,
    hackathon_date,
  } = data;
  return (
    <Card className="mb-4">
      <CardBody>
        <CardTitle>
          <span className="h2">{name}</span>
          <a href={hackathon_website}>
            <i className="ml-2 simple-icon-link" />
          </a>
          <Separator className="mb-2" />
        </CardTitle>

        <div className="h4">Application</div>
        <CardSubtitle>
          <IntlMessages id="hackcard.starts" />
        </CardSubtitle>
        <CardText>{application_start}</CardText>

        <CardSubtitle>
          <IntlMessages id="hackcard.ends" />
        </CardSubtitle>
        <CardText>{application_end}</CardText>

        <Separator className="mb-2" />

        <div className="h4">Hackathon</div>
        <CardSubtitle>
          <IntlMessages id="hackcard.starts" />
        </CardSubtitle>
        <CardText>{hackathon_start}</CardText>

        <CardSubtitle>
          <IntlMessages id="hackcard.ends" />
        </CardSubtitle>
        <CardText>{hackathon_date}</CardText>

        <Badge color={getStatusBadgeColor(status)} pill className="mb-3">
          {getStatus(status)}
        </Badge>

        <Separator className="mb-3" />

        <Row className="justify-content-center">
          {participated ? null : (
            <Button
              href={`${adminRoot}/challenges/register/${id}`}
              color="secondary"
              className="m-2"
            >
              <IntlMessages id="hackcard.participate-button" />
            </Button>
          )}
          <Button
            href={`${adminRoot}/challenges/${id}`}
            color="secondary"
            className="m-2"
          >
            <IntlMessages id="hackcard.view-details" />
          </Button>
        </Row>
      </CardBody>
    </Card>
  );
};

export default RecentHack;
